import io from 'socket.io-client'
import {
    AUTH_SUCCESS,
    ERROR_MSG,
    RECEIVE_USER,
    RESET_USER,
    RECEIVE_USER_LIST,
    RECEIVE_MSG_LIST,
    RECEIVE_MSG,
    MSG_READED
} from './Action-types'

import {
    reqRegister,
    reqLogin,
    reqUpdateUser,
    reqUser,
    reqUserList,
    reqChatMsgList,
    reqReadMsg
} from '../api'




// action
const authSuccess = (user) => ({type: AUTH_SUCCESS, data: user})
//action
const errorMsg = (msg) => ({type: ERROR_MSG, data: msg})
// action
const receiveUser = (user) => ({type: RECEIVE_USER, data: user})
// action
export const resetUser = (msg) => ({type: RESET_USER, data: msg})
// action
export const receiveUserList = (userList) => ({type: RECEIVE_USER_LIST, data: userList})
// action
const receiveMsgList = ({users, chatMsgs, userid}) => ({
  type: RECEIVE_MSG_LIST,
  data: {users, chatMsgs, userid}
})
// action
const receiveMsg = (chatMsg, userid) => ({
  type: RECEIVE_MSG,
  data: {chatMsg, userid}
})
// action

const msgReaded = ({count, from, to}) => ({
  type: MSG_READED,
  data: {count, from, to}
})


// action
export const register = (user) => {
    const {
        username, 
        password, 
        password2, 
        type
    } = user

    if(!username) {
        return errorMsg('username is a MUST')
    } else if(password !== password2) {
        return errorMsg('ensure SAME password')
    } 
        
    return async dispatch => {
    // 
    // promise,then
    /* const promise = reqRegister(user)
    promise.then(response => {
        const result = response.data
    }) */
    // 
    const response = await reqRegister({
      username,
      password,
      type
    })
    console.log("register -> response", response)
    const result = response.data
    if (result.code === 0) {
      getMsgList(dispatch, result.data._id)
      // action
      console.log(result.data)
      dispatch(authSuccess(result.data))
    } else {
      // action
      dispatch(errorMsg(result.msg))
    }
  }
}




// action
export const login = (user) => {
    const {username, password} = user
    if(!username) {
        return errorMsg('username is a MUST')
    }
    if(!password) {
        return errorMsg('PASSWORD is a MUST')
    }

    return async dispatch => {
        const response = await reqLogin({
          username,
          password
        })
        const result = response.data
        if (result.code === 0) {
          getMsgList(dispatch, result.data._id)
          dispatch(authSuccess(result.data))
        } else {
          dispatch(errorMsg(result.msg))
        }
      }
    }

    // updating user  action
export const updateUser = (user) => {
  return async dispatch => {
    const response = await reqUpdateUser(user)
    const result = response.data
    if(result.code === 0) {
      dispatch(receiveUser(result.data))
    } else {
      dispatch(resetUser(result.data))
    }
  }
}

// getting user aysn action
export const getUser = () => {
  return async dispatch => {
    const response = await reqUser()
    const result = response.data
    if (result.code === 0) {
      getMsgList(dispatch, result.data._id)
      dispatch(receiveUser(result.data))
    } else {
      dispatch(resetUser(result.msg))
    }
  }
}

// user listaction
export const getUserList = (type) => {
  return async dispatch => { //ajax 
    const response = await reqUserList(type)
    const result = response.data
    //action
    if(result.code === 0) {
      dispatch(receiveUserList(result.data))
    }
  }
}

// sending messageaction
export const sendMsg = ({from, to, content}) => {
  return dispatch => {
    console.log('send msg', {from, to, content})
    // initIO()
    io.socket.emit('sendMsg', {from, to, content})
  }
}

// action
export const readMsg = (from, to) => {
  return async dispatch => {
    const response = await reqReadMsg(from)
    const result = response.data
    if (result.code === 0) {
      const count = result.data
      dispatch(msgReaded({count, from, to}))
    }
  }
}

/* socket
* 1.
* 2.
*/
// init wescoket
function initIO(dispatch, userid) {
  // io is both function and obj
  if (!io.socket) {
//    io.socket = io('ws://localhost:8080')

    // saving obj
    io.socket.on('receiveMsg', function(chatMsg) {
      console.log('www', chatMsg)
      // only when chatMsg matchs current user => save to dispatching action 
      if (userid === chatMsg.from || userid === chatMsg.to) {
        dispatch(receiveMsg(chatMsg, userid))
      }
    })
  }
}
// ()
async function getMsgList(dispatch, userid) {
  initIO(dispatch, userid) //user succssfully logged in => initIO

  const response = await reqChatMsgList()
  const result = response.data
  if (result.code === 0) {
    const {users, chatMsgs} = result.data
    // action
    dispatch(receiveMsgList({users, chatMsgs, userid}))
  }
}