/*
nreducer
stateactionstate
*/
import {combineReducers} from 'redux'

import {
    AUTH_SUCCESS,
    ERROR_MSG,
    RECEIVE_USER,
    RESET_USER,
    RECEIVE_USER_LIST,
    RECEIVE_MSG_LIST,
    RECEIVE_MSG,
    MSG_READED
} from './Action-types'

import {getRedirectTo} from '../utils/index'

const initUser = {
    username: '',
    type: '',
    msg: '',
    redirectTo: '' // 
}

//userreducer
function user(state = initUser, action) {
    switch (action.type) {
        case AUTH_SUCCESS: //data user
        const {type, avatar} = action.data
            return {...state, ...action.data, redirectTo: getRedirectTo(type, avatar)}
        case ERROR_MSG: //data msg
            return {...state, msg: action.data}
        case RECEIVE_USER: //data USER
            return action.data
        case RESET_USER: //data msg
            return {...initUser, msg: action.data}
        default:
            return state
        }
}

const initUserList = []
// userlistreducer
function userList(state=initUserList, action) {
    switch (action.type) {
        case RECEIVE_USER_LIST: //data is userList
        return action.data
        default:
            return state
    }
}

const initChat = {
    // 
    users: {},
    // 
    chatMsgs: [],
    // 
    unReadCount: 0
  }
  function chat(state=initChat, action) {
    switch (action.type) {
      case RECEIVE_MSG_LIST:
        const {users, chatMsgs, userid} = action.data
        console.log("chat -> action.data", action.data)
        return {
          users,
          chatMsgs,
          // message
          unReadCount: chatMsgs.reduce((preTotal, msg) => preTotal + (!msg.read && msg.to === userid ? 1 : 0), 0)
        }
      case RECEIVE_MSG:
        // const {chatMsg, userid} = action.data
        const {chatMsg} = action.data
        // debugger
        return {
          users: state.users,
          chatMsgs: [...state.chatMsgs, chatMsg],
          unReadCount: state.unReadCount + (!chatMsg.read && chatMsg.to === action.data.userid ? 1 : 0)
        }
      case MSG_READED:
        const {from, to, count} = action.data
        // state.chatMsgs.forEach(msg => {
        //   if (msg.from === from && msg.to === to && !msg.read) {
        //     msg.read = true
        //   }
        // })
        return {
          users: state.users,
          chatMsgs: state.chatMsgs.map(msg => {
            // 
            if (msg.from === from && msg.to === to && !msg.read) {
              // msgread: true,msg
              /* msg.read = true
              return msg */
              return {...msg, read: true}
            } else {
              // 
              return msg
            }
          }),
          unReadCount: state.unReadCount - count
        }
      default:
        return state
    }
  }

//  {user: { }}
export default combineReducers({
    user,
    userList,
    chat
})

